@import "config"; 

.category{
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
}

.info-category{
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;    
}

.tittle-category{
    @include centerAll;
    font-family: $tittleFont;
    letter-spacing: 1px;
    font-size: 60px;
    color: $secondColor;
    margin:0 5px 1px 10px;
    padding:0;
}

.image-category{
    @include size(30px,30px);
    margin-left: 10px;
}

.description-category{
    font-size: 17px;
    font-family: $textFont;
    color: $secondColor;
    margin-left: 10px;
}

.subcategory .tittle-category{
    font-size: 40px;
}

.list-products{
    margin:0;
    padding:0;
    margin-left: 20px;
    list-style: none;
}


.info-category .view-more{
    margin:0;
    margin-left:10px;
    padding: 0;
} 
