@import "config";

.rotating {
    width: 50px;
    height: 50px;
    animation: spin 1s linear infinite;
    animation:spin 1s linear infinite;
    -moz-animation:spin 1s linear infinite;
    animation:spin 1s linear infinite;
}
@-moz-keyframes spin { 
    100% { -moz-transform: rotate(360deg); } 
}
@-webkit-keyframes spin { 
    100% { -webkit-transform: rotate(360deg); } 
}
@keyframes spin { 
    100% { 
        -webkit-transform: rotate(360deg); 
        transform:rotate(360deg); 
    } 
}

.boton-not-pressed{
    //border: 2px solid #906A3B;
    //border-bottom: 1px solid #906A3B;
    //border-left: 1px solid #906A3B;
    //border-right: 1px solid #906A3B;
    border-top: 3px solid #775023;
    box-shadow: -3px 3px 3px #775023,
                3px 3px 3px #775023;
}


.boton-pressed{
    border: 1px solid #775023;
}

.info-product{
    font-family: $textFont;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin:0;
    padding:0;
    margin-right: 5px;
    margin-top: 5px;
    padding: 5px;
    border-radius: 10px;
    border-spacing: 10px;
}

.tittle-product{
    display: flex;
    align-items: center;
}

.features-product{
    display: flex;
    flex-direction: column;
}

.name-product{
    text-transform: uppercase;
    font-size: 15px;
    margin:0;
}

.details-product{
    text-transform: uppercase;
    font-size: 10px;
}


.prices-products{
    
    @include centerAll();
    flex-direction: row;
    height: fit-content;
    padding:0;
}

.price-product{
    @include centerAll();
    @include size(70px,30px);
    @include borderRadius(10%);
    margin-left: 1px;
    margin-right: 1px;
    list-style: none;
    flex-direction: column;
    background-color: $secondColor;
    color:rgb(245, 214, 214);
}

.description-price{
    margin:0;
    padding:0;
    padding-top: 8px; 
    font-size: 8px;
    text-transform: uppercase;
    color:white;
}

.multiple-price::before, .single-price::before{
    content: '$';
}

.multiple-price{
    margin:0;
    padding: 0;
    padding-bottom: 5px;
    font-size: 15px;
    color:white;
}

.single-price{
    margin:0;
    padding: 0;
    padding-bottom: 5px;
    font-size: 15px;
    color:black;
}

.description-container{
    margin-top: 10px;
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
}

.description-container-edit{
    @include centerAll();
    margin-top: 10px;
}

.description-product{
    @include centerAll();
    @include borderRadius(10%);
    transition: all 0s;
    position: static;
    font-family: $textFont;  
    font-size: 12px;
    text-align: left;
    margin:2px;
    padding: 10px;
}

.description-product-view{

    @include borderRadius(0);
    transition: all 0s;
    font-family: $textFont;
    font-size: 18px;
    width: 90vw; 
    text-align: justify;
    transform: translate(-10px,0);
    margin-top:10px;
    margin-bottom: 10px;
}


.description-product-edit{
    font-family: $textFont;
    font-size:10px;
}

.image-product{
    @include size(150px,100px);
    @include borderRadius(10%);
    max-width: 150px;
    max-height: 100px;
    transition: width 0.8s, height 0.8s;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.3);
    position: static;
    transform: translate(0,0);
    object-fit: cover;
}

.image-product-view{
    max-width: 100vw;
    min-width: 100vw;
    transition: all 0.8s;
    cursor: pointer;
    width: 100vw;
    border-radius: 0;
    object-fit: contain;
}






