@import "config";

.btn-next-page:focus,
.btn-next-page:active,
.btn-next-page:hover,
.btn-prev-page:focus,
.btn-prev-page:active,
.btn-prev-page:hover {
    outline:0;
    border-radius: 50%;
}

.view-more{
    @include size(30px,30px);
    @include borderRadius(50%);
    @include buttonStyles;
    margin-right:10px;
    background-color: $secondColor;
    color: white;
}

.btn-config-container{
    @include centerAll;
    @include borderRadius(10%);
    background-color: rgba(0,0,0,0.3);
    gap:10px;
    color:$secondColor;
    left: 50%;
    right: 50%;
}

.btn-config{
    @include buttonStyles;
    @include centerAll;
    @include borderRadius(10%);
    background-color: rgba(0,0,0,0.4);
}

.svg-next-page, .svg-prev-page{
    @include buttonStyles;
    @include size(50px,50px);
    @include borderRadius(50%);
    background-color: rgba(255,255,255,0.1);
}


.btn-next-page, .btn-prev-page{
    @include size(60px,60px);
    @include buttonStyles;
    @include borderRadius(50%);
    @include centerAll;
    background-color: $bgBtn;
    color:$secondColor;
    font-size: 20px;
    margin:10px;
}

.waiting-menu strong{
    color:$secondColor;
}

.waiting-menu{
    
    @include formatSpacing;
    @include size(100vw,100vh);
    @include centerAll;

    position:fixed;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 60px;
    
    flex-direction: column;
    font-family: $tittleFont;
    color:$secondColor;    
}

.finger-pointing {
    animation: MoveUpDown 1s linear infinite;
    position: fixed;
    top: 0;
    left: 20px;
    font-size: 30px;
    z-index: 1000000000000;
  }
  
  @keyframes MoveUpDown {
    0%, 100% {
      transform: translateY(70px);
    }
    50% {
      transform: translateY(100px);
    }
  }


